import { SharedModule } from "app/shared/shared.module";
import { NgModule, NO_ERRORS_SCHEMA } from "@angular/core";
import { Routes, RouterModule } from "@angular/router";
import { InActiveTasksComponent } from "app/task/in-activetasks/in-activetasks.component";

export const routes: Routes = [{ path: "", component: InActiveTasksComponent }];

@NgModule({
  imports: [SharedModule, RouterModule.forChild(routes)],
  declarations: [InActiveTasksComponent],
  schemas: [NO_ERRORS_SCHEMA]
})
export class InActiveTaskModule {}



